import { transform } from '../../../unit/const'

export default {
  props: [
    'active',
    'color',
    'size',
    'top',
    'left',
    'transformScale',
    'label',
    'position',
    'arrow'
  ],

  computed: {
    transformCSS () {
      return `transform: scale(${this.transformScale})`
    }
  }
}


