import store from '../vuex/store'
import todo from './todo'

const keyboard = {
  37: 'left',
  38: 'rotate',
  39: 'right',
  40: 'down',
  32: 'space',
  83: 's',
  82: 'r',
  80: 'p'
}

let keydownActive

const boardKeys = Object.keys(keyboard).map(e => parseInt(e, 10))

const keyDown = e => {
  if (e.metaKey === true || boardKeys.indexOf(e.keyCode) === -1) {
    return
  }
  const type = keyboard[e.keyCode]
  if (type === keydownActive) {
    return
  }
  keydownActive = type
  todo[type].down(store)
}

const keyUp = e => {
  if (e.metaKey === true || boardKeys.indexOf(e.keyCode) === -1) {
    return
  }
  const type = keyboard[e.keyCode]
  if (type === keydownActive) {
    keydownActive = ''
  }
  todo[type].up(store)
}

document.addEventListener('keydown', keyDown, true)
document.addEventListener('keyup', keyUp, true)


const gamepadkeyboard = {
  14: 'left',
  12: 'rotate',
  15: 'right',
  13: 'down',
  0: 'space',
  2: 's',
  1: 'r',
  3: 'p'
}

function logGamepadState() {
  const gamepads = navigator.getGamepads();
  if (gamepads[0]) {
    const gamepad = gamepads[0];

    // 按钮状态
    gamepad.buttons.forEach((button, index) => {
      if (button.pressed) {
        console.log(`按钮 ${index} 被按下`);

        const type = gamepadkeyboard[index]
        if (type === keydownActive) {
          return
        }
        keydownActive = type
        todo[type].down(store)
        setTimeout(() => {
          // keyup simulation
          const type = gamepadkeyboard[index]
          if (type === keydownActive) {
            keydownActive = ''
          }
          todo[type].up(store)
        }, 100);
      }
    });

    // // 轴状态
    // gamepad.axes.forEach((axis, index) => {
    //     console.log(`轴 ${index} 值: ${axis.toFixed(2)}`);
    // });
  }
  requestAnimationFrame(logGamepadState);
}

logGamepadState();

window.addEventListener("gamepadconnected", (event) => {
  const gamepad = event.gamepad;
  console.log(`手柄已连接：${gamepad.id}`);
});

window.addEventListener("gamepaddisconnected", (event) => {
  console.log(`手柄已断开：${event.gamepad.id}`);
});



